header {
  background: linear-gradient(to bottom, var(--Color03), var(--Color05),var(--Color03));
  width: 95%;
  display: flex;
  flex-direction: column;
  padding: 2em 2em 200px;
  margin-top: 40px;
  cursor: text;
  clip-path: polygon(
    0% 0%,
    100% 0%,
    100% 75%,
    30% 75%,
    10% 100%,
    10% 75%,
    0% 75%
  );
}
header h1::selection,
header h2::selection,
header p::selection {
  background-color: var(--Color02);
  color: var(--Color05);
}
header h1 {
  font-family: var(--FontPrimary);
  margin-bottom: 1em;
  font-size: 2em;
  font-weight: 900;
  letter-spacing: 1em;
  color: var(--Color02);
  text-transform: uppercase;
  transition: all ease 0.5s;
  -webkit-transition: all ease 0.5s;
  -moz-transition: all ease 0.5s;
  -ms-transition: all ease 0.5s;
  -o-transition: all ease 0.5s;
}
header:hover h1 {
  letter-spacing: 3px;
}
header h2 {
  margin-bottom: 10px;
  font-family: var(--FontPrimary);
  word-spacing: 5px;
  font-size: 3em;
  background: linear-gradient(
    to bottom,
    var(--Color01),
    var(--Color04),
    var(--Color01)
  );
  background-clip: text;
  -webkit-text-fill-color: transparent;
  transition: all ease 0.5s;
  -webkit-transition: all ease 0.5s;
  -moz-transition: all ease 0.5s;
  -ms-transition: all ease 0.5s;
  -o-transition: all ease 0.5s;
}
header:hover h2 {
  text-shadow: 1px 1px 2px var(--Color04);
}
header p {
  font-family: var(--FontSecondary);
  font-size: 1.5em;
  font-weight: 500;
  color: #000;
}
