:root {
  --Purple: #6200ea;
  --Green: #00ea62;
  --Orange: #ea6200;
  /* Paleta de Colores */
  --ColdPurple: #9988dd;
  --ColdNavyBlue: #2527ff;
  --ColdSkyBlue: #1099bb;
  --ColdGreen: #15ab92;
  --ColdPink: #f3aacb;
  /* Colores escogido para alenky */
  --Color01: #0d070e;
  --Color02: #7b1f80;
  --Color03: #b76dbe;
  --Color04: #aa9eab;
  --Color05: #e6d5eb;
  /* Fuentes a usar */
  --FontPrimary: "DM Serif Text", serif;
  --FontSecondary: "Nunito Sans", serif;
}
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  /* position: relative; */
}
#root {
  display: flex;
  background-color: #0d070e;
  background-image: url(../components/img/polygon-scatter-haikei.svg);
  background-repeat: repeat-y;
  background-size: contain;
  flex-direction: column;
  align-items: center;
  overflow: hidden;
}
@media only screen and (max-width: 767px) {
  #root{
    max-width: 100vw;
    min-height: 100vh;
    /* background-blend-mode:saturation ; */
   }
}

@media only screen and (min-width: 768px) {
  #root{
    max-width: 100vw;
    min-height: 100vh;
    /* background-blend-mode:saturation ; */
   }
}

@media only screen and (min-width: 1000px) and (any-hover: hover) {
  #root {
    max-width: 100vw;
    min-width: 100vw;
    min-height: 100vh;
    /* background-blend-mode:normal; */
  }
}
