.Navigator {
  z-index: 10;
  position: sticky;
  top: 0;
  left: 0;
  padding: 0;
  margin-top: 10px;
  display: flex;
  background: linear-gradient(
    to bottom,
    var(--Color02),
    var(--Color03),
    var(--Color02)
  );
}
.Navigator button {
  background: transparent;
  width: 48px;
  height: 48px;
  font-size: 2em;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  transition: all ease 0.5s;
  -webkit-transition: all ease 0.5s;
  -moz-transition: all ease 0.5s;
  -ms-transition: all ease 0.5s;
  -o-transition: all ease 0.5s;
}
.Navigator button:hover {
  background: radial-gradient(var(--Color01), var(--Color05));
  color: #fff;
}

.Navigator ul li {
  list-style-type: none;
}
.Navigator ul li a {
  transition: all ease 0.5s;
  font-size: 1.25em;
  font-family: var(--FontPrimary);
  color: var(--Color05);
  text-decoration: none;
  -webkit-transition: all ease 0.5s;
  -moz-transition: all ease 0.5s;
  -ms-transition: all ease 0.5s;
  -o-transition: all ease 0.5s;
  clip-path: polygon(0% 0%, 90% 0%, 100% 50%, 90% 100%, 0% 100%, 10% 50%);
}
.Navigator ul li a:hover {
  background: var(--Color01);
  text-decoration: underline;
}
@media only screen and (max-width: 767px) {
  .Navigator {
    width: 100%;
    flex-direction: column;
  }
  .Navigator button {
    width: 48px;
    height: 48px;
  }
  .Navigator ul {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
  .Navigator ul li{
    width: 100%;
    display: flex;
  }
  .Navigator ul li a {
    padding: 8px 24px;
  }
}

@media only screen and (min-width: 768px) {
  .Navigator {
    width: 100%;
    flex-direction: column;
  }
  .Navigator button {
    width: 48px;
    height: 48px;
  }
  .Navigator ul {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
  }
  .Navigator ul li{
    width: 100%;
    display: flex;
  }
  .Navigator ul li a {
    padding: 8px 24px;
  }
}

@media only screen and (min-width: 1000px) and (any-hover: hover) {
  .Navigator {
    width: 100%;
    flex-direction: row;
  }
  .Navigator button {
    width: 48px;
    height: 48px;
  }
  .Navigator ul {
    display: flex;
    flex-direction: row;
    align-items: center;
  }
  .Navigator ul li a {
    padding: 8px 24px;
  }
}
