main {
  z-index: 1;
  width: 90%;
  display: flex;
  flex-direction: column;
  border: 5px solid var(--Color05);
  border-radius: 30px;
  margin: 100px 0;
  -webkit-border-radius: 30px;
  -moz-border-radius: 30px;
  -ms-border-radius: 30px;
  -o-border-radius: 30px;
}
main section {
  width: 100%;
  background: radial-gradient(circle, var(--Color05), var(--Color01));
  border: 5px solid var(--Color05);
  border-radius: 30px;
  display: flex;
  flex-direction: row;
  align-items: center;
  position: relative;
  padding: 5em 1em 1em;
  -webkit-border-radius: 30px;
  -moz-border-radius: 30px;
  -ms-border-radius: 30px;
  -o-border-radius: 30px;
  overflow-y: hidden;
  transition: all ease 0.5s;
  -webkit-transition: all ease 0.5s;
  -moz-transition: all ease 0.5s;
  -ms-transition: all ease 0.5s;
  -o-transition: all ease 0.5s;
}

main section h2 {
  word-spacing: 1em;
  font-size: 2em;
  text-align: center;
  font-family: var(--FontPrimary);
  color: var(--Color01);
  background: var(--Color03);
  line-height: 2em;
}
main section article {
  display: flex;
  flex-direction: column;
  padding: 1em 2em;
  background: var(--Color05);
  border: transparent 5px solid;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
  margin: 0 10px;
  transition: all ease 0.5s;
  -webkit-transition: all ease 0.5s;
  -moz-transition: all ease 0.5s;
  -ms-transition: all ease 0.5s;
  -o-transition: all ease 0.5s;
}
main section article h3 {
  font-size: 1.5em;
  margin-bottom: 1em;
  text-align: center;
  color: var(--Color01);
  transition: all ease 0.5s;
  -webkit-transition: all ease 0.5s;
  -moz-transition: all ease 0.5s;
  -ms-transition: all ease 0.5s;
  -o-transition: all ease 0.5s;
}

main section article img {
  width: 180px;
  aspect-ratio: 4/5;
  transition: all ease 0.5s;
  -webkit-transition: all ease 0.5s;
  -moz-transition: all ease 0.5s;
  -ms-transition: all ease 0.5s;
  -o-transition: all ease 0.5s;
}
/* mobile */
@media only screen and (max-width: 767px) {
  main {
    padding: 0.5em 1em;
  }
  main section {
    margin: 0.5em 0;
    overflow-x: scroll;
  }
  main section h2 {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }
  main section article img {
    clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
  }
  main section article img.show,
  main section article:hover img {
    clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
  }
}
/* tablet */
@media only screen and (min-width: 768px) {
  main {
    padding: 0.75em 1.5em;
  }
  main section {
    margin: 0.75em 0;
    overflow-x: visible;
  }
  main section h2 {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }
  main section article img {
    clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
  }
  main section article img.show,
  main section article:hover img {
    clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
  }
}
/* desktop */
@media only screen and (min-width: 1000px) and (any-hover: hover) {
  main {
    padding: 1em 2em;
  }
  main section {
    margin: 1em 0;
    overflow-x: hidden;
  }
  main section h2 {
    width: 100%;
    position: absolute;
    top: 0;
    left: 0;
  }
  main section article img {
    clip-path: polygon(50% 0%, 100% 50%, 50% 100%, 0% 50%);
  }
  main section:hover {
    transition: all ease 0.5s;
    background: transparent;
    -webkit-transition: all ease 0.5s;
    -moz-transition: all ease 0.5s;
    -ms-transition: all ease 0.5s;
    -o-transition: all ease 0.5s;
  }
  main section article:hover {
    background: transparent;
    border: 5px solid var(--Color05);
  }
  main section article:hover h3 {
    color: var(--Color05);
  }
  main section article:hover img {
    clip-path: polygon(0% 0%, 100% 0%, 100% 100%, 0% 100%);
  }
}
