footer {
  background: #3e3e3e;
  clip-path: polygon(
    0 0,
    5% 10%,
    10% 0,
    15% 10%,
    20% 0,
    25% 10%,
    30% 0,
    35% 10%,
    40% 0,
    45% 10%,
    50% 0,
    55% 10%,
    60% 0,
    65% 10%,
    70% 0,
    75% 10%,
    80% 0,
    85% 10%,
    90% 0,
    95% 10%,
    100% 0,
    100% 100%,
    0 100%
  );
  width: 100%;
  height: 120vh; 
  overflow: hidden;
  position: relative;
  bottom: 0;
  z-index: 11;
}
footer .AboutMe {
  padding: 20px;
}
footer .AboutMe h4 {
  font-family: var(--FontPrimary);
  font-size: 1.5em;
  color: var(--Color03);
  letter-spacing: 2px;
  font-weight: 500;
  user-select: none;
}
footer .AboutMe p {
  font-size: 1em;
  font-family: var(--FontSecondary);
  color: var(--Color05);
}
footer .AboutMe p::selection {
  background: var(--Color05);
  color: var(--Color02);
}

footer .LinksFooter a {
  padding-top: 10px;
  text-decoration: none;
  color: var(--Color03);
  font-family: var(--FontSecondary);
  word-break: keep-all;
  font-size: 1.2em;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  transition: all ease 0.5s;
  -webkit-transition: all ease 0.5s;
  -moz-transition: all ease 0.5s;
  -ms-transition: all ease 0.5s;
  -o-transition: all ease 0.5s;
  position: relative;
}
footer .LinksFooter a::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  background: var(--Color03);
  width: 0;
  height: 0;
  border-radius: 20px;
  -webkit-border-radius: 20px;
  -moz-border-radius: 20px;
  -ms-border-radius: 20px;
  -o-border-radius: 20px;
  transition: all ease 0.5s;
  -webkit-transition: all ease 0.5s;
  -moz-transition: all ease 0.5s;
  -ms-transition: all ease 0.5s;
  -o-transition: all ease 0.5s;
}
