@media only screen and (max-width: 767px) {
  footer {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  footer .LinksFooter {
    display: grid;
    grid-template-columns: auto auto;
  }
}

@media only screen and (min-width: 768px) {
  footer {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(3, 1fr);
    gap: 0;
    padding: 100px 16px 30px;
  }
  footer .AboutMe {
    grid-area: 1 / 1 / 3 / 3;
  }
  footer .LinksFooter {
    display: grid;
    grid-template-columns: auto;
    grid-area: 1 / 3 / 3 / 6;
  }
}

@media only screen and (min-width: 1000px) and (any-hover: hover) {
  /* Styles here */
  footer {
    display: grid;
    grid-template-columns: repeat(5, 1fr);
    grid-template-rows: repeat(3, 1fr);
    gap: 0;
    padding: 200px 16px 60px;
  }
  footer .AboutMe {
    grid-area: 1 / 1 / 3 / 3;
  }
  footer .LinksFooter {
    display: grid;
    grid-template-columns: auto auto;
    row-gap: 15px;
    column-gap: 5px;
    grid-area: 1 / 3 / 3 / 6;
  }
  footer .LinksFooter a:hover {
    color: var(--Color05);
    text-shadow: 1px 1px 5px var(--Color02);
    transition: all ease 0.5s;
    -webkit-transition: all ease 0.5s;
    -moz-transition: all ease 0.5s;
    -ms-transition: all ease 0.5s;
    -o-transition: all ease 0.5s;
  }
  footer .LinksFooter a:hover::before {
    width: 100%;
    height: 5px;
    left: 0;
  }
  footer .SocialMedia a:hover img {
    background: var(--Color02);
    transition: all ease 0.5s;
    -webkit-transition: all ease 0.5s;
    -moz-transition: all ease 0.5s;
    -ms-transition: all ease 0.5s;
    -o-transition: all ease 0.5s;
    border: 2px solid var(--Color05);
  }
}
